.how-we-work {
    position: relative;
    padding: 6rem 0;
    overflow: hidden;
    background: linear-gradient(135deg, #fff5f2 0%, #fff 100%);
}

.how-we-work-background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
}

.how-we-work-header {
    text-align: center;
    margin-bottom: 4rem;
    position: relative;
    z-index: 1;
}

.subtitle {
    font-family: var(--subtitle-font);
    color: var(--text-dark);
    font-size: 1.2rem;
    margin-top: 1rem;
    opacity: 0;
    animation: fadeIn 0.6s ease-out 0.3s forwards;
}

.process-timeline {
    position: relative;
    z-index: 1;
    padding: 2rem 0;
}

.timeline-line {
    position: absolute;
    top: 120px;
    left: 50%;
    transform: translateX(-50%);
    height: 2px;
    width: 80%;
    background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
    opacity: 0.3;
}

.process-step {
    background: white;
    border-radius: 20px;
    padding: 2rem;
    height: 100%;
    position: relative;
    transition: all 0.3s ease;
    box-shadow: 0 10px 30px rgba(0,0,0,0.1);
    border: 2px solid transparent;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.process-step::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 0;
}

.process-step.active {
    transform: translateY(-10px);
    border-color: var(--primary-color);
}

.process-step.active::before {
    opacity: 0.05;
}

.step-icon {
    position: relative;
    width: 80px;
    height: 80px;
    background: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 1.5rem;
    box-shadow: 0 5px 15px rgba(0,0,0,0.1);
    transition: all 0.3s ease;
}

.step-icon .icon {
    font-size: 2rem;
    transition: transform 0.3s ease;
}

.step-icon .number {
    position: absolute;
    top: -5px;
    right: -5px;
    width: 30px;
    height: 30px;
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    border-radius: 50%;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 0.9rem;
}

.process-step.active .step-icon {
    transform: scale(1.1) rotate(5deg);
}

.process-step.active .step-icon .icon {
    transform: scale(1.1);
}

.step-title {
    font-family: var(--decorative-font);
    color: var(--secondary-color);
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    text-align: center;
    position: relative;
    z-index: 1;
}

.step-content {
    position: relative;
    z-index: 1;
    width: 100%;
}

.step-content ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.step-content li {
    color: var(--text-dark);
    font-size: 0.9rem;
    line-height: 1.6;
    margin-bottom: 1rem;
    padding-left: 1.5rem;
    position: relative;
    font-weight: 500;
    text-align: left;
}

.step-content li::before {
    content: '→';
    position: absolute;
    left: 0;
    color: var(--primary-color);
    transition: transform 0.3s ease;
}

.process-step.active .step-content li::before {
    transform: translateX(5px);
}

@keyframes fadeIn {
    to { opacity: 1; }
}

@media (max-width: 992px) {
    .timeline-line {
        display: none;
    }
}

@media (max-width: 768px) {
    .how-we-work {
        padding: 4rem 0;
    }

    .process-step {
        margin-bottom: 1.5rem;
        padding: 1.5rem;
    }

    .step-content li {
        font-size: 0.85rem;
    }

    .animated-circle {
        display: none;
    }
}